<template>
    <div>
        <loading :active.sync="isLoading" ></loading> 
        <table class="table mt-4">
            <thead>
                <tr>
                    <th width="80"></th>
                    <th width="130">訂購時間</th>
                    <th width="150">E-mail</th>
                    <th width="">購買款項</th>
                    <th width="100">應付金額</th>
                    <th width="110" >是否付款</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in orders" :key="item.id" >
                    <td class="align-middle">
                        <a href="#" @click.prevent="openmodel(item)" v-if="!item.is_paid" data-toggle="modal" class="btn btn-outline-warning btn-sm">修改</a>
                    </td>
                    <td v-if="!isNaN(item.paid_date)" class="text-left">{{item.paid_date | timestamp}}</td>
                    <td v-else></td>
                    <td>{{item.user.email}}</td>
                    <td>
                        <div v-for="product in item.products" :key="product.id">
                            {{product.product.title}} 數量 : {{product.qty}} {{product.product.unit}}
                        </div> 
                    </td>
                    <td class="text-right">{{item.total | currency }}</td>
                    <td class="text-center">
                        <span v-if="item.is_paid" class="text-success">已付款</span>
                        <span v-else class="text-secondary">未付款</span>
                    </td>
                </tr>
            </tbody>
        </table>
        <!-- Pagination -->
        <!-- <Pagination :pagination="pagination" v-on:getPageProducts="getProducts"/> -->
        <!-- Modal -->
        <div class="modal fade" id="orderModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form>
                        <div class="form-group">
                            <label for="nameFormControl" >收件人姓名</label>
                            <input type="text" v-model="tempOrder.user.name" class="form-control" id="nameFormControl">
                        </div>
                        <div class="form-group">
                            <label for="emailFormControl">收件人E-mail</label>
                            <input type="email" v-model="tempOrder.user.email" class="form-control" id="emailFormControl">
                        </div>
                        <div class="form-group">
                            <label for="telFormControl">收件人電話</label>
                            <input type="tel" v-model="tempOrder.user.tel" class="form-control" id="telFormControl">
                        </div>
                        <div class="form-group">
                            <label for="addressFormControl">收件人地址</label>
                            <input type="address" v-model="tempOrder.user.address" class="form-control" id="addressFormControl">
                        </div>
                    </form>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
                    <button type="button" class="btn btn-primary" @click="changeOrder(tempOrder.id)">確認修改</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import $ from 'jquery';
import Pagination from '../components/Pagination';

export default {
    data: function() {
        return {
            orders : {
            },
            tempOrder :{
                user:{

                },
            },
            isNew : false,
            isDelete : false,
            isLoading: false,
            // pagination: {},
        };
    },
    components: {
        Pagination,
    },
    methods: { 
        getOrders(page = 1) {
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${page}`;
            const vm = this;
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
                vm.orders = response.data.orders;
                vm.isLoading = false;
                vm.pagination = response.data.pagination;
            });
        },
        openmodel(item){
            this.tempOrder = Object.assign({},item);
            $('#orderModal').modal('show')
            console.log(this.tempOrder)
        },
        changeOrder(id){
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/order/${id}`;
            const vm = this;
            vm.isLoading = true;
            this.$http.put(api,{data:vm.tempOrder}).then((response) => {
                console.log(response.data)
                if(response.data.success){
                    $('#orderModal').modal('hide');
                    vm.getOrders()
                }else {
                    $('#orderModal').modal('hide');
                    vm.getOrders()
                    console.log('失敗')
                }
                vm.isLoading = false;
                vm.pagination = response.data.pagination;
            });
        }
    },
    created(){
        this.getOrders();
    }
}
</script>
